import React, {useEffect, useState} from 'react';
import {Button, Container, Form, Table} from 'react-bootstrap';
import {useParams} from "react-router-dom";
import axios from 'axios';

function QnaDetail() {

    const [resArr, setResArr] = useState([]);
    const params = useParams();

    useEffect(() => {
        axios.get('/user/qna/qnaDetail',
            {
                params: {id: params.id}
            }).then(response => {
                setResArr(response.data);
            }
        )
            .catch(error => console.log(error))
    }, []);

    const defaultImg = `${process.env.PUBLIC_URL}/404.jpg`;
    const handleImgError = (e) => {
        e.target.src = defaultImg; //        
    };

    return (
        <div className="divTable">
            <Container>
                <Form>
                    <Table striped bordered hover>
                        <tbody>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    type="text"
                                    placeholder=" "
                                    value={resArr.title || ''}
                                    readOnly
                                />
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    type="text"
                                    placeholder=" "
                                    value={resArr.content || ''}
                                    readOnly
                                />
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    type="text"
                                    placeholder=""
                                    value={resArr.author || ''}
                                    readOnly={true}
                                />
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label> </Form.Label></td>
                            <td>
                                {/*    */}
                                {resArr.fileNm && (
                                    <div>
                                        {resArr.filePath && (
                                            <img
                                                src={`${process.env.PUBLIC_URL}/upload/img/${resArr.fileNm}`}
                                                alt=""
                                                style={{width: '250px', height: '300px'}}
                                            />
                                        )}
                                        <p>: {resArr.fileNm || ''}</p>
                                    </div>
                                )}
                            </td>
                        </tr>
                        <tr>
                            <td colSpan="2" className="text-center">
                                <Button variant="primary" type="button" href={'/qnaList'}>
                                    
                                </Button>
                                &nbsp;&nbsp;
                                <Button variant="primary" type="button" href={`/qnaModify/${resArr.id}`}>
                                    
                                </Button>
                            </td>
                        </tr>
                        </tbody>
                    </Table>
                </Form>
            </Container>
        </div>
    );
}

export default QnaDetail;