import React, {useEffect, useRef, useState} from 'react';
import {Button, Container, Form, Table} from 'react-bootstrap';
import {useNavigate, useParams} from "react-router-dom";
import axios from 'axios';
import ImageEditor from '../../../../js/utils/ImageEditor';
import {formatPrice, addCommasToPrice} from "../../../../js/utils/formatUtils";

function ProductModify() {

    //const [resArr, setResArr] = useState([]);
    const params = useParams();

    //     
    //const [inputValue, setInputValue] = useState('');

    //     
    const [inputValue, setInputValue] = useState({
        productNm: '',
        price: '',
        content: '',
        author: '',
        file: '',
    });
    const { productNm, price, content, author,file } = inputValue;
    // onChange  
    const handleChange = (e) => {

        // input  name  value 
        const { name, value } = e.target;

        // price   formatPrice    
        const updatedValue = name === 'price' ? addCommasToPrice(value) : value;

        //  
        setInputValue({
            ...inputValue,
            [name]: updatedValue
        });
    };

    useEffect(() => {
        axios.get('/admin/product/productModify',
            {
                params: {id: params.id}
            }).then(response => {
                //setResArr(response.data);
                setInputValue(response.data);
            }
        )
            .catch(error => console.log(error))
    }, []);

    const defaultImg = `${process.env.PUBLIC_URL}/404.jpg`;
    const handleImgError = (e) => {
        e.target.src = defaultImg; //        
    };

    const [productKind, setProductKind] = useState(inputValue.productKind);
    const options = [
        {value: 'clothing', label: ''},
        {value: 'electronics', label: ''},
        {value: 'books', label: ''},
        {value: 'food', label: ''},
        {value: 'toys', label: ''}
    ];

    const initialImage = `/upload/img/${inputValue.fileNm}`

    const navigate = useNavigate();

    //
    const handleSubmit = (e) => {

        e.preventDefault();

        const msg = window.confirm(" ?");
        const formData = new FormData(e.target);
        const price = formData.get("price").replace(/,/g, ""); // g     
        formData.set("price", price); // price   

        const data = Object.fromEntries(formData.entries());

        if(!formData.get("productNm")){
            alert(" .");
        }else if(!formData.get("price")){
            alert(" .");
        }else if(!formData.get("content")){
            alert(" .");
        }else{
            if(msg){
                axios.post('/admin/product/updateProduct', formData)
                    .then(response => {
                        //console.log('Post submitted successfully');
                        alert(" .");
                        // useHistory import     
                        //     
                        navigate(`/productDetail/${params.id}`);
                    })
                    .catch(error => {
                        console.error('Error submitting post: ', error);
                    });
            }
        }
    };

    //
    const handleDelete = () => {
        const msg = window.confirm(" ?");
        if (msg){
            axios.get('/admin/product/deleteProduct',
                {
                    params: {id: params.id}
                })
                .then(response => {
                    //console.log('Post submitted successfully');
                    alert(" .");
                    // useHistory import     
                    //     
                    navigate(`/productList`);
                })
                .catch(error => {
                    console.error('Error submitting post: ', error);
                });
        }
    };


    return (
        <div className="divTable">
            <Container>
                <Form onSubmit={handleSubmit}>
                    <Table striped bordered hover>
                        <tbody>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    name="productNm"
                                    type="text"
                                    placeholder=" ."
                                    value={inputValue.productNm || ''}
                                    onChange={handleChange}
                                />
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    name="price"
                                    className="price"
                                    type="text"
                                    placeholder=" ."
                                    value={addCommasToPrice(inputValue.price) || ''}
                                    onChange={handleChange}
                                />
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    name="content"
                                    className="content"
                                    as="textarea"
                                    rows={5}
                                    placeholder=" ."
                                    value={inputValue.content || ''}
                                    onChange={handleChange}
                                />
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    name="productKind"
                                    className="productKind"
                                    as="select"
                                    style={{width: '100px', height: '35px'}}
                                    value={productKind || ''} //    
                                    onChange={(e) => setProductKind(e.target.value)} //   
                                >
                                    {options.map(option => (
                                        <option
                                            key={option.value}
                                            value={option.value}
                                        >
                                            {option.label}
                                        </option>
                                    ))}
                                </Form.Control>
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    name="author"
                                    className="author"
                                    type="text"
                                    placeholder=""
                                    defaultValue=""
                                    readOnly={true}
                                />
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label> </Form.Label></td>
                            <td>
                                <ImageEditor initialImage={initialImage}/>
                            </td>
                        </tr>
                        <tr>
                            <td colSpan="2" className="text-center">
                                <input type="hidden" name="id" className="id" value={params.id || ''} readOnly/>
                                <Button variant="primary" type="submit" className="updateBtn">
                                    
                                </Button>
                                &nbsp;&nbsp;
                                <Button variant="primary" type="button" className="deleteBtn" onClick={handleDelete}>
                                    
                                </Button>
                            </td>
                        </tr>
                        </tbody>
                    </Table>
                </Form>
            </Container>
        </div>
    );
}

export default ProductModify;