import React, {useState} from 'react';
import {Container, Form, Button, Col} from 'react-bootstrap';
import axios from "axios";
import {useNavigate} from "react-router-dom";

const Join = () => {
    const [userNm, setUserNm] = useState('');
    const [email, setEmail] = useState('');
    const [userId, setUserId] = useState('');
    const [pw, setPw] = useState('');
    const [phone, setPhone] = useState('');
    const [address, setAddress] = useState('');
    const [role, setRole] = useState('user'); // :  

    //      
    const [errors, setErrors] = useState({
        userNm: '',
        email: '',
        userId: '',
        pw: '',
        phone: '',
        address: '',
    });

    const navigate = useNavigate();

    //   
    const handleSubmit = (e) => {
        e.preventDefault();
        //   
        if (validateForm()) {
            const msg = window.confirm("?");
            const formData = new FormData(e.target);
            const data = Object.fromEntries(formData.entries());

            axios.post('/api/join', formData)
                .then(response => {
                    //console.log('Post submitted successfully');
                    alert(" .");
                    // useHistory import     
                    //     
                    navigate(`/`);
                })
                .catch(error => {
                    console.error('Error submitting post: ', error);
                });

            console.log('Submitting:', userNm, email, userId, pw, phone, address, role);
        } else {
            console.log('   ');
        }
    };

    //    
    const validateForm = () => {
        let valid = true;
        const newErrors = {
            userNm: '',
            email: '',
            userId: '',
            pw: '',
            phone: '',
            address: '',
        };

        //    
        if (!userNm.trim()) {
            newErrors.userNm = ' .';
            valid = false;
        }

        //    
        if (!email.trim()) {
            newErrors.email = ' .';
            valid = false;
        } else if (!/\S+@\S+\.\S+/.test(email)) {
            newErrors.email = '   .';
            valid = false;
        }

        //    
        if (!userId.trim()) {
            newErrors.userId = ' .';
            valid = false;
        }

        //    
        if (!pw.trim()) {
            newErrors.pw = ' .';
            valid = false;
        } else if (pw.length < 6) {
            newErrors.pw = '  6  .';
            valid = false;
        }

        //    
        if (!phone.trim()) {
            newErrors.phone = ' .';
            valid = false;
        } else if (!/^\d{3}-\d{3,4}-\d{4}$/.test(phone)) {
            newErrors.phone = '    (: 010-1234-5678).';
            valid = false;
        }

        //    
        if (!address.trim()) {
            newErrors.address = ' .';
            valid = false;
        }

        //   
        setErrors(newErrors);

        return valid;
    };

    return (
        <div>
            <Container>
                <h1 className="text-center mt-4"></h1>
                <Form className="custom-form" onSubmit={handleSubmit}>
                    <Form.Group className="mb-3" controlId="formUserId">
                        <Form.Label></Form.Label>
                        <Form.Control
                            name="userId"
                            type="text"
                            placeholder=" "
                            value={userId}
                            onChange={(e) => setUserId(e.target.value)}
                            className={errors.userId ? 'form-control is-invalid' : 'form-control'}
                        />
                        {errors.userId && <Form.Control.Feedback type="invalid">{errors.userId}</Form.Control.Feedback>}
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formPw">
                        <Form.Label></Form.Label>
                        <Form.Control
                            name="pw"
                            type="pw"
                            placeholder=" "
                            value={pw}
                            onChange={(e) => setPw(e.target.value)}
                            className={errors.pw ? 'form-control is-invalid' : 'form-control'}
                        />
                        {errors.pw && <Form.Control.Feedback type="invalid">{errors.pw}</Form.Control.Feedback>}
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formUserNm">
                        <Form.Label></Form.Label>
                        <Form.Control
                            name="userNm"
                            type="text"
                            placeholder=" "
                            value={userNm}
                            onChange={(e) => setUserNm(e.target.value)}
                            className={errors.userNm ? 'form-control is-invalid' : 'form-control'}
                        />
                        {errors.userNm && <Form.Control.Feedback type="invalid">{errors.userNm}</Form.Control.Feedback>}
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formEmail">
                        <Form.Label></Form.Label>
                        <Form.Control
                            name="email"
                            type="email"
                            placeholder=" "
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            className={errors.email ? 'form-control is-invalid' : 'form-control'}
                        />
                        {errors.email && <Form.Control.Feedback type="invalid">{errors.email}</Form.Control.Feedback>}
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formPhone">
                        <Form.Label></Form.Label>
                        <Form.Control
                            name="phone"
                            type="text"
                            placeholder="  (: 010-1234-5678)"
                            value={phone}
                            onChange={(e) => setPhone(e.target.value)}
                            className={errors.phone ? 'form-control is-invalid' : 'form-control'}
                        />
                        {errors.phone && <Form.Control.Feedback type="invalid">{errors.phone}</Form.Control.Feedback>}
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formAddress">
                        <Form.Label></Form.Label>
                        <Form.Control
                            name="address"
                            as="textarea"
                            rows={3}
                            placeholder=" "
                            value={address}
                            onChange={(e) => setAddress(e.target.value)}
                            className={errors.address ? 'form-control is-invalid' : 'form-control'}
                        />
                        {errors.address && <Form.Control.Feedback type="invalid">{errors.address}</Form.Control.Feedback>}
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formRole">
                        <Form.Label></Form.Label>
                        <Col>
                            <Form.Check
                                type="radio"
                                label=" "
                                name="role"
                                id="user"
                                checked={role === 'user'}
                                onChange={() => setRole('user')}
                            />
                            <Form.Check
                                type="radio"
                                label=""
                                name="role"
                                id="seller"
                                checked={role === 'seller'}
                                onChange={() => setRole('seller')}
                            />
                        </Col>
                    </Form.Group>

                    <Button variant="primary" type="submit">
                        
                    </Button>
                </Form>
            </Container>
        </div>
    );
};

export default Join;