import React, { useState, useRef } from 'react';
import { Form, Button, Container } from 'react-bootstrap';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

function ProductRegist() {
    //
    const productNmRef = useRef();
    const priceRef = useRef();
    const contentRef = useRef();
    const authorRef = useRef();
    const productKindRef = useRef();
    const fileRef = useRef();
    const navigate = useNavigate();

    const handleSubmit = event => {

        event.preventDefault();

        const formData = new FormData();
        formData.append('productNm', productNmRef.current.value);
        formData.append('price', priceRef.current.value);
        formData.append('content', contentRef.current.value);
        formData.append('author', authorRef.current.value);
        formData.append('productKind', productKindRef.current.value);
        formData.append('file', fileRef.current.files[0]); //    

        axios.post('/admin/product/insertProduct', formData)
            .then(response => {
                // console.log('Post submitted successfully');
                alert(" .");
                // useHistory import     
                //     
                navigate('/productList');
            })
            .catch(error => {
                console.error('Error submitting post: ', error);
            });
    };

    //
    const [selectedFile, setSelectedFile] = useState(null);

    const fileSelectedHandler = event => {
        setSelectedFile(event.target.files[0]);
        //console.log(event.target.files[0]);
    };

    const fileUploadHandler = () => {
        const formData = new FormData();
        formData.append('file', selectedFile);

        axios.post('/utill/imgUpload', formData)
            .then(response => {
                //console.log(response.data);
                // Handle success, e.g., show a success message
            })
            .catch(error => {
                console.error('Error uploading file: ', error);
                // Handle error, e.g., show an error message
            });
    };

    return (
        <div>
            <Container>
                <Form onSubmit={handleSubmit}>
                    <Form.Group controlId="formTitle">
                        <Form.Label></Form.Label>
                        <Form.Control type="text" placeholder=" " ref={productNmRef} />
                    </Form.Group>

                    <Form.Group controlId="formPrice">
                        <Form.Label></Form.Label>
                        <Form.Control type="number" placeholder=" "  ref={priceRef} />
                    </Form.Group>

                    <Form.Group controlId="formContent">
                        <Form.Label></Form.Label>
                        <Form.Control as="textarea" rows={5} placeholder=" " ref={contentRef} />
                    </Form.Group>

                    <Form.Group controlId="formProductKind">
                        <Form.Label></Form.Label>
                        <Form.Control as="select" style={{width: '100px', height: '35px'}} ref={productKindRef}>
                            <option value="clothing"></option>
                            <option value="electronics"></option>
                            <option value="books"></option>
                            <option value="food"></option>
                            <option value="toys"></option>
                        </Form.Control>
                    </Form.Group>

                    <Form.Group controlId="formAuthor">
                        <Form.Label></Form.Label>
                        <Form.Control type="text" placeholder="" ref={authorRef} readOnly={true}/>
                    </Form.Group>

                    <Form.Group controlId="formImage">
                    <Form.Label> </Form.Label>
                        <Form.Control type="file" ref={fileRef} onChange={fileSelectedHandler} />
                    </Form.Group>

                    <Button variant="primary" type="submit">
                        
                    </Button>
                </Form>
            </Container>
            {/*<button onClick={fileUploadHandler}>Upload</button>*/}
        </div>
    );
}

export default ProductRegist;