import React, {useEffect, useState} from 'react';
import {Button, Container, Form, Table} from 'react-bootstrap';
import {useParams} from "react-router-dom";
import axios from 'axios';
import {formatPrice} from "../../../../js/utils/formatUtils";

function ProductDetail() {

    const [resArr, setResArr] = useState([]);
    const params = useParams();

    useEffect(() => {
        axios.get('/admin/product/productDetail',
            {
                params: {id: params.id}
            }).then(response => {
                setResArr(response.data);
            }
        )
            .catch(error => console.log(error))
    }, []);

    const defaultImg = `${process.env.PUBLIC_URL}/404.jpg`;
    const handleImgError = (e) => {
        e.target.src = defaultImg; //        
    };

    const [productKind, setProductKind] = useState(resArr.productKind);
    const options = [
        {value: 'clothing', label: ''},
        {value: 'electronics', label: ''},
        {value: 'books', label: ''},
        {value: 'food', label: ''},
        {value: 'toys', label: ''}
    ];

    return (
        <div className="divTable">
            <Container>
                <Form>
                    <Table striped bordered hover>
                        <tbody>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    type="text"
                                    placeholder=" "
                                    value={resArr.productNm || ''}
                                    readOnly
                                />
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    type="text"
                                    placeholder=" "
                                    value={formatPrice(resArr.price)}
                                    readOnly
                                />
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    as="textarea"
                                    rows={5}
                                    placeholder=" "
                                    value={resArr.content || ''}
                                    readOnly
                                />
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    as="select"
                                    style={{width: '100px', height: '35px'}}
                                    value={productKind || ''} //    
                                    onChange={(e) => setProductKind(e.target.value)} //   
                                    disabled
                                >
                                    {options.map(option => (
                                        <option
                                            key={option.value}
                                            value={option.value}
                                            //      
                                        >
                                            {option.label}
                                        </option>
                                    ))}
                                </Form.Control>
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    type="text"
                                    placeholder=""
                                    readOnly={true}
                                />
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label> </Form.Label></td>
                            <td>
                                {/*    */}
                                {resArr.fileNm && (
                                    <div>
                                        {resArr.filePath && (
                                            <img
                                                src={`${process.env.PUBLIC_URL}/upload/img/${resArr.fileNm}`}
                                                alt=""
                                                style={{width: '250px', height: '300px'}}
                                            />
                                        )}
                                        <p>: {resArr.fileNm || ''}</p>
                                    </div>
                                )}
                            </td>
                        </tr>
                        <tr>
                            <td colSpan="2" className="text-center">
                                <Button variant="primary" type="button" className="deleteBtn" href={'/productList'}>
                                    
                                </Button>
                                &nbsp;&nbsp;
                                <Button variant="primary" type="button" href={`/productModify/${resArr.id}`}>
                                    
                                </Button>
                            </td>
                        </tr>
                        </tbody>
                    </Table>
                </Form>
            </Container>
        </div>
    );
}

export default ProductDetail;