import React, {useEffect, useRef, useState} from 'react';
import {Button, Container, Form, Table} from 'react-bootstrap';
import {useNavigate, useParams} from "react-router-dom";
import axios from 'axios';
import ImageEditor from '../../../js/utils/ImageEditor';

function QnaModify() {

    //const [resArr, setResArr] = useState([]);
    const params = useParams();

    //     
    const [inputValue, setInputValue] = useState({
        titlte: '',
        content: '',
        author: '',
        file: '',
    });
    const { titlte, content, author,file } = inputValue;
    // onChange  
    const handleChange = (e) => {

        // input  name  value 
        const { name, value } = e.target;

        // price   formatPrice    
        const updatedValue = value;

        //  
        setInputValue({
            ...inputValue,
            [name]: updatedValue
        });
    };

    useEffect(() => {
        axios.get('/user/qna/qnaModify',
            {
                params: {id: params.id}
            }).then(response => {
                //setResArr(response.data);
                setInputValue(response.data);
            }
        )
            .catch(error => console.log(error))
    }, []);

    const defaultImg = `${process.env.PUBLIC_URL}/404.jpg`;
    const handleImgError = (e) => {
        e.target.src = defaultImg; //        
    };

    const initialImage = `/upload/img/${inputValue.fileNm}`;

    const navigate = useNavigate();

    //
    const handleSubmit = (e) => {

        e.preventDefault();

        const msg = window.confirm(" ?");
        const formData = new FormData(e.target);

        const data = Object.fromEntries(formData.entries());

        if(!formData.get("title")){
            alert(" .");
        }else if(!formData.get("content")){
            alert(" .");
        }else{
            if(msg){
                axios.post('/user/qna/updateQna', formData)
                    .then(response => {
                        //console.log('Post submitted successfully');
                        alert(".");
                        // useHistory import     
                        //    
                        navigate(`/qnaDetail/${params.id}`);
                    })
                    .catch(error => {
                        console.error('Error submitting post: ', error);
                    });
            }
        }
    };

    //
    const handleDelete = () => {
        const msg = window.confirm(" ?");
        if (msg){
            axios.get('/user/qna/deleteQna',
                {
                    params: {id: params.id}
                })
                .then(response => {
                    //console.log('Post submitted successfully');
                    alert(".");
                    // useHistory import     
                    //     
                    navigate(`/qnaList`);
                })
                .catch(error => {
                    console.error('Error submitting post: ', error);
                });
        }
    };


    return (
        <div className="divTable">
            <Container>
                <Form onSubmit={handleSubmit}>
                    <Table striped bordered hover>
                        <tbody>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    name="title"
                                    type="text"
                                    placeholder=" ."
                                    value={inputValue.title || ''}
                                    onChange={handleChange}
                                />
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    name="content"
                                    className="content"
                                    as="textarea"
                                    rows={5}
                                    placeholder=" ."
                                    value={inputValue.content || ''}
                                    onChange={handleChange}
                                />
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label></Form.Label></td>
                            <td>
                                <Form.Control
                                    name="author"
                                    className="author"
                                    type="text"
                                    placeholder=""
                                    defaultValue=""
                                    readOnly={true}
                                />
                            </td>
                        </tr>
                        <tr>
                            <td><Form.Label> </Form.Label></td>
                            <td>
                                <ImageEditor initialImage={initialImage}/>
                            </td>
                        </tr>
                        <tr>
                            <td colSpan="2" className="text-center">
                                <input type="hidden" name="id" className="id" value={params.id || ''} readOnly/>
                                <Button variant="primary" type="submit" className="updateBtn">
                                    
                                </Button>
                                &nbsp;&nbsp;
                                <Button variant="primary" type="button" className="deleteBtn" onClick={handleDelete}>
                                    
                                </Button>
                            </td>
                        </tr>
                        </tbody>
                    </Table>
                </Form>
            </Container>
        </div>
    );
}

export default QnaModify;